$main-bg: #fff;
$main-color: #000;
$text-color: #adadad;

$txt-main-color: #000;
$txt-second-color: #8d8d8d;
$txt-white: #fff;

$btn-main-bg: $main-color;
$btn-main-color: #fff;

$header-height: 100px;
$header-tablet-height: 70px;
$header-mobile-height: 70px;
$header-shrink-height: 70px;

$box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;

$colors: (
    'blue': #4267b2,
    'orange': #fbb96b,
    'pink': #fe7e73,
    'white': #fff,
    'black': #000,
    'main': $main-color,
    'red': red,
    'yellow': yellow,
);

$mobile-width: 600px;
$tablet-width: 1024px;

$numbers: (0, 1, 2, 3, 4, 5, 6, 7, 8);
