.cart {
    @include flex(flex-start, flex-start);

    @include mobile {
        flex-wrap: wrap;
    }

    &_info {
        width: 40%;
        margin-right: 30px;
        margin-top: 30px;
        padding: 20px;
        box-shadow: $box-shadow;
        position: sticky;
        top: calc(#{$header-shrink-height} + 20px);

        @include mobile {
            width: 100%;
            margin-right: 0;
            margin-bottom: 30px;
            position: unset;
        }

        &_txt {
            & > * {
                margin-bottom: 10px;
            }

            p {
                font-size: 1.25rem;
            }
            &_price {
                @include flex(center, space-between);
                padding: 10px 0;
                font-size: 1.25rem;

                span:nth-child(2) {
                    font-size: 2rem;
                    color: $main-color;
                    font-weight: 600;
                }
            }
        }
        &_btn {
            &_checkout {
                margin-bottom: 20px;
            }
        }
    }

    &_list {
        flex-grow: 1;
        margin-top: 30px;
    }

    &_item {
        @include flex(center, flex-start);
        margin-bottom: 20px;

        &_img {
            // width: 10%;
            margin-right: 20px;

            img {
                height: 150px;
            }
        }

        &_info {
            @include flex(center, space-between);
            flex-grow: 1;

            @include mobile {
                @include flex(flex-start, center);
                flex-direction: column;

                & > * {
                    margin-bottom: 10px;
                }
            }

            &_name {
                width: 40%;

                @include mobile {
                    width: 100%;
                }
            }

            &_name,
            &_price {
                font-size: 1.25rem;
            }

            &_del {
                font-size: 1.5rem;

                i {
                    cursor: pointer;

                    &:hover {
                        color: $main-color;
                    }
                }
            }
        }
    }
}
